import { graphql, PageProps } from "gatsby";

import { useTranslation } from "gatsby-plugin-react-i18next";

import Layout from "../components/layout";
import Seo from "../components/seo";
import Conference from "../components/pages/conference";

import useEditionData from "../hooks/useEditionData";
import React from "react";

const ConferencePage: React.FC<PageProps> = ({ location }) => {
  const { t } = useTranslation();

  const editionData = useEditionData(2023);

  return (
    <Layout>
      <Seo title={t("title")} pathname={location.pathname} />

      <Conference editionData={editionData} />
    </Layout>
  );
};

export default ConferencePage;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(
      filter: {
        ns: { in: ["common", "conference"] }
        language: { eq: $language }
      }
    ) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
