import { useTranslation } from "gatsby-plugin-react-i18next";

import Card from "react-bootstrap/Card";
import { EditionData } from "../../data";

import useEventDates from "../../hooks/useEventDates";
import { CardData, cards } from "../general/cards";

import * as styles from "./conference.module.css";
import React from "react";
import { Button } from "react-bootstrap";

type ConferenceCardProps = {
  card: CardData;
};

const ConferenceCard: React.FC<ConferenceCardProps> = ({ card }) => {
  return (
    <Card className={"card"} text={"dark"}>
      <Card.Header className={"textMuted cardHeader"}>{card.time}</Card.Header>

      <Card.Body className={"cardBody"}>
        <Card.Title className={"cardTitle"}>{card.title}</Card.Title>
        <Card.Subtitle className={"cardSubtitle"}>
          <Card.Link className={"a textMuted cardLink"} href={card.contact}>
            {card.speaker}
          </Card.Link>
        </Card.Subtitle>
        <Card.Text>{card.details}</Card.Text>
      </Card.Body>
    </Card>
  );
};

type conferenceDetails = {
  title: string;
  content: string[];
};

type ConferenceListProps = {
  item: conferenceDetails;
  className: string;
};

const ConferenceList: React.FC<ConferenceListProps> = ({ item, className }) => {
  return (
    <div className={className}>
      <p className={"p"}>{item.title}</p>
      <ul>
        {item.content.map((content, index) => (
          <li key={"conferenceDetails" + index} className={"li"}>
            {content}
          </li>
        ))}
      </ul>
    </div>
  );
};

type ConferenceProps = {
  editionData: EditionData;
};

const Conference: React.FC<ConferenceProps> = ({ editionData }) => {
  const { t } = useTranslation();

  const edition = editionData.edition;
  const { conference: date } = useEventDates(editionData);
  const details = t("conferenceDetails", {
    returnObjects: true,
  }) as conferenceDetails[];

  return (
    <div className={`page ${styles.conference}`} id={"conference"}>
      <h2 className={"date"}>{date}</h2>
      <h1 className={"title"}>{t("title")}</h1>
      {/* Button for applying to present at the conference */}
      {edition > 2023 && (
        <Button
          className={`buttonLink`}
          target={"_blank"}
          rel={"noreferrer"}
          href={
            "https://forms.zohopublic.eu/nitrolanguageprocessing/form/nscriereNitroConferine/formperma/qP1qRf1y7vpsa6zMnNbX5GphnEd-HCc5VrY4fst-Ysw"
          }
        >
          {t("button")}
        </Button>
      )}

      {/*Conference list of cards*/}
      {edition < 2024 && (
        <ul className={"ul"}>
          {cards[edition].map((item, index) => (
            <ConferenceCard key={"conferenceCard" + index} card={item} />
          ))}
        </ul>
      )}

      {/* Details for applying to the conference */}
      {edition > 2023 && (
        <div className={styles.conferenceDetails}>
          <ConferenceList item={details[0]} className={"left"} />
          <ConferenceList item={details[1]} className={"right"} />
          <p className={"p"}>{t("extraDetails")}</p>
        </div>
      )}

      {/* Link for joining the conference online */}
      {edition === 2023 && (
        <Button
          className={"buttonLink"}
          target={"_blank"}
          rel={"noreferrer"}
          href={"http://conf.nitronlp.rocks"}
        >
          {t("link")}
        </Button>
      )}

      {/* The conference has ended */}
      {edition < 2023 && <p className={`center p`}>{t("endDescription")}</p>}
    </div>
  );
};

export default Conference;
